import React, { Component } from 'react';
import './App.css';
import '../Task/Task'
import Task from '../Task/Task';

class App extends Component {
  state={
    tasks:[
      {task:"Task one", status:"todo"},
      {task:"Task two", status:"complete"}
    ],
    value : "Enter Task Details Here ..."
  };

  textInput = React.createRef();

  addTask = (event) => {
    if(this.textInput.current.value === ""){
      this.setState({value : "Please enter a Task here to add."});
      event.preventDefault();
      
    }
    else{
    let t = [...this.state.tasks];
    t.push({task:this.textInput.current.value, status:"todo"
    });
    this.setState({tasks:t, value : "Enter Task Details Here ..."});
    event.preventDefault();
    this.textInput.current.value = "";
    }
  }

  deleteTask = (taskIndex) => {
    const tasks = this.state.tasks;
    tasks[taskIndex].status="complete";
    this.setState({tasks : tasks});
  }

  componentDidMount(){
    this.textInput.current.focus();
  }

  componentWillUpdate(){
    this.textInput.current.focus();
  }

  render() {


    return (
      <div className="App">
      <div className="Head">
        <form onSubmit={this.addTask}>
          <input id="textInput" type="text" placeholder={this.state.value} ref={this.textInput}/>
          <button> Add Task</button>
        </form>
      </div>
      {
        this.state.tasks.map((task, index)=>{
          return(
            <div key = {index}>
                <Task task={task.task} status={task.status} click={this.deleteTask.bind(this, index)}> </Task>
            </div>
          );
        })
      }
      <div className="footerText">
        Click on the task to mark it as complete.
      </div>
      </div>
    );
  }
}

export default App;



